export const store = configureStore({
    reducer: {
        auth: authReducer,
        app: appReducer,
        todolists: todolistsReducer,
        tasks: tasksReducer,
    },
})

// export const store = createStore(rootReducer, applyMiddleware(thunkMiddleware))
